import { UserService } from './userService';

export interface User {
  id: string;
  name: string;
  email: string;
  role: 'employee';
}

export class UserAuthService {
  private static readonly USER_SESSION_KEY = 'user_auth_session';
  private static readonly SESSION_DURATION = 8 * 60 * 60 * 1000; // 8ÏãúÍ∞Ñ

  /**
   * ÏùºÎ∞ò ÏÇ¨Ïö©Ïûê Î°úÍ∑∏Ïù∏
   */
  static async login(email: string, password: string): Promise<{ success: boolean; user?: User; error?: string }> {
    try {
      const dbUser = await UserService.authenticateUser(email, password);

      if (!dbUser) {
        return { success: false, error: 'Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.' };
      }

      if (!dbUser.is_active) {
        return { success: false, error: 'ÎπÑÌôúÏÑ±ÌôîÎêú Í≥ÑÏ†ïÏûÖÎãàÎã§. Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌïòÏÑ∏Ïöî.' };
      }

      const user: User = {
        id: dbUser.id!.toString(),
        name: dbUser.full_name,
        email: dbUser.email,
        role: 'employee'
      };

      const token = this.generateToken(user.id);
      const sessionData = {
        token,
        user,
        loginTime: Date.now(),
        expiryTime: Date.now() + this.SESSION_DURATION
      };

      localStorage.setItem(this.USER_SESSION_KEY, JSON.stringify(sessionData));
      return { success: true, user };
    } catch (error) {
      console.error('Login error:', error);
      return { success: false, error: 'Î°úÍ∑∏Ïù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.' };
    }
  }

  /**
   * Î°úÍ∑∏ÏïÑÏõÉ
   */
  static logout(): void {
    localStorage.removeItem(this.USER_SESSION_KEY);
  }

  /**
   * ÌòÑÏû¨ Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú ÌôïÏù∏
   */
  static isAuthenticated(): boolean {
    const sessionData = this.getSessionData();
    if (!sessionData) return false;

    // ÏÑ∏ÏÖò ÎßåÎ£å ÌôïÏù∏
    if (Date.now() > sessionData.expiryTime) {
      this.logout();
      return false;
    }

    return true;
  }

  /**
   * ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î∞òÌôò
   */
  static getCurrentUser(): (User & { loginTime: number }) | null {
    const sessionData = this.getSessionData();
    if (!sessionData || !this.isAuthenticated()) return null;

    return {
      ...sessionData.user,
      loginTime: sessionData.loginTime
    };
  }

  /**
   * ÏÑ∏ÏÖò Ïó∞Ïû•
   */
  static extendSession(): void {
    const sessionData = this.getSessionData();
    if (sessionData && this.isAuthenticated()) {
      sessionData.expiryTime = Date.now() + this.SESSION_DURATION;
      localStorage.setItem(this.USER_SESSION_KEY, JSON.stringify(sessionData));
    }
  }

  /**
   * ÏÑ∏ÏÖò ÎßåÎ£åÍπåÏßÄ ÎÇ®ÏùÄ ÏãúÍ∞Ñ (Î∞ÄÎ¶¨Ï¥à)
   */
  static getTimeUntilExpiry(): number {
    const sessionData = this.getSessionData();
    if (!sessionData) return 0;

    return Math.max(0, sessionData.expiryTime - Date.now());
  }

  /**
   * Î°úÍ∑∏Ïù∏ Í∞ÄÎä•Ìïú ÏÇ¨Ïö©Ïûê Î™©Î°ù Ï°∞Ìöå (Í∞úÎ∞ú/ÌÖåÏä§Ìä∏Ïö©)
   */
  static async getLoginEnabledUsers(): Promise<Array<{ email: string; name: string; hasTemp: boolean }>> {
    try {
      const allUsers = await UserService.getAllUsers();
      return allUsers
        .filter(user => user.password_hash && user.is_active)
        .map(user => ({
          email: user.email,
          name: user.full_name,
          hasTemp: !!user.is_password_temp
        }));
    } catch (error) {
      console.error('Error getting login enabled users:', error);
      return [];
    }
  }

  /**
   * ÌÜ†ÌÅ∞ ÏÉùÏÑ±
   */
  private static generateToken(userId: string): string {
    return btoa(`${userId}:${Date.now()}:${Math.random()}`);
  }

  /**
   * ÏÑ∏ÏÖò Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
   */
  private static getSessionData(): {
    token: string;
    user: User;
    loginTime: number;
    expiryTime: number;
  } | null {
    try {
      const data = localStorage.getItem(this.USER_SESSION_KEY);
      return data ? JSON.parse(data) : null;
    } catch {
      return null;
    }
  }
}