import React, { useState, useEffect } from 'react';
import { UserAuthService, User } from '../services/userAuthService';
import UserLogin from './UserLogin';
import WorkDashboard from './WorkDashboard';
import WorkTimeInput from './WorkTimeInput';
import '../styles/components/UserApp.css';

type UserPageType = 'dashboard' | 'input';

const UserApp: React.FC = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [currentUser, setCurrentUser] = useState<(User & { loginTime: number }) | null>(null);
  const [currentPage, setCurrentPage] = useState<UserPageType>('dashboard');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Ï¥àÍ∏∞ Ïù∏Ï¶ù ÏÉÅÌÉú ÌôïÏù∏
    const checkAuth = () => {
      const authStatus = UserAuthService.isAuthenticated();
      const user = UserAuthService.getCurrentUser();

      setIsAuthenticated(authStatus);
      setCurrentUser(user);
      setIsLoading(false);
    };

    checkAuth();

    // ÏÑ∏ÏÖò ÎßåÎ£åÎ•º Ï£ºÍ∏∞Ï†ÅÏúºÎ°ú Ï≤¥ÌÅ¨ (1Î∂ÑÎßàÎã§)
    const interval = setInterval(() => {
      const authStatus = UserAuthService.isAuthenticated();
      const user = UserAuthService.getCurrentUser();

      if (authStatus !== isAuthenticated) {
        setIsAuthenticated(authStatus);
        setCurrentUser(user);
      }
    }, 60000);

    return () => clearInterval(interval);
  }, [isAuthenticated]);

  const handleLoginSuccess = () => {
    const user = UserAuthService.getCurrentUser();
    setIsAuthenticated(true);
    setCurrentUser(user);
  };

  const handleLogout = () => {
    const confirmLogout = window.confirm('Ï†ïÎßê Î°úÍ∑∏ÏïÑÏõÉÌïòÏãúÍ≤†ÏäµÎãàÍπå?');
    if (confirmLogout) {
      UserAuthService.logout();
      setIsAuthenticated(false);
      setCurrentUser(null);
      setCurrentPage('dashboard');
    }
  };

  const renderCurrentPage = () => {
    switch (currentPage) {
      case 'dashboard':
        return <WorkDashboard />;
      case 'input':
        return <WorkTimeInput />;
      default:
        return <WorkDashboard />;
    }
  };

  // Ï¥àÍ∏∞ Î°úÎî© ÏÉÅÌÉú
  if (isLoading) {
    return (
      <div className="user-app-loading">
        <div className="loading-content">
          <div className="loading-icon">‚öôÔ∏è</div>
          <div className="loading-text">Í∑ºÎ¨¥ÏãúÍ∞Ñ Í¥ÄÎ¶¨ ÏãúÏä§ÌÖú ÏãúÏûë Ï§ë...</div>
        </div>
      </div>
    );
  }

  // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Î°úÍ∑∏Ïù∏ ÌôîÎ©¥ ÌëúÏãú
  if (!isAuthenticated || !currentUser) {
    return <UserLogin onLoginSuccess={handleLoginSuccess} />;
  }

  // Ïù∏Ï¶ùÎêú Í≤ΩÏö∞ Î©îÏù∏ Ïï± ÌëúÏãú
  return (
    <div className="user-app">
      {/* Navigation */}
      <nav className="user-nav">
        <div className="nav-header">
          <div className="brand">
            <div className="brand-icon">‚è∞</div>
            <div className="brand-content">
              <h1 className="brand-title">Í∑ºÎ¨¥ÏãúÍ∞Ñ Í¥ÄÎ¶¨</h1>
              <p className="brand-subtitle">Work Time Tracker</p>
            </div>
          </div>
        </div>

        <div className="nav-menu">
          <div className="menu-section">
            <div className="menu-label">Î©îÏù∏ Î©îÎâ¥</div>
            <button
              className={`nav-item ${currentPage === 'dashboard' ? 'active' : ''}`}
              onClick={() => setCurrentPage('dashboard')}
            >
              <span className="nav-icon">üìä</span>
              <span className="nav-text">ÎåÄÏãúÎ≥¥Îìú</span>
            </button>
            <button
              className={`nav-item ${currentPage === 'input' ? 'active' : ''}`}
              onClick={() => setCurrentPage('input')}
            >
              <span className="nav-icon">‚è∞</span>
              <span className="nav-text">Í∑ºÎ¨¥ÏãúÍ∞Ñ ÏûÖÎ†•</span>
            </button>
          </div>
        </div>

        <div className="nav-footer">
          {/* User Info */}
          <div className="user-info">
            <div className="user-details">
              <div className="user-avatar">üë§</div>
              <div className="user-text">
                <span className="user-name">{currentUser.name}</span>
                <span className="user-email">{currentUser.email}</span>
                <span className="login-time">
                  Î°úÍ∑∏Ïù∏: {new Date(currentUser.loginTime).toLocaleString('ko-KR', {
                    month: '2-digit',
                    day: '2-digit',
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </span>
              </div>
            </div>
            <button className="logout-btn" onClick={handleLogout} title="Î°úÍ∑∏ÏïÑÏõÉ">
              üö™
            </button>
          </div>

          {/* Version Info */}
          <div className="version-info">
            <span className="version-text">v1.0.0</span>
            <span className="company">¬© Work Tracker</span>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="user-main">
        {renderCurrentPage()}
      </main>
    </div>
  );
};

export default UserApp;