import React, { useState, useEffect } from 'react';
import { WorkTimeService, WorkTime } from '../services/workTimeService';
import { UserAuthService } from '../services/userAuthService';
import '../styles/components/WorkTimeInput.css';

const WorkTimeInput: React.FC = () => {
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [startTime, setStartTime] = useState('09:00');
  const [endTime, setEndTime] = useState('18:00');
  const [breakTime, setBreakTime] = useState(60);
  const [notes, setNotes] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [existingRecord, setExistingRecord] = useState<WorkTime | null>(null);
  const [toast, setToast] = useState<{ show: boolean; message: string; type: 'success' | 'error' }>({
    show: false,
    message: '',
    type: 'success'
  });

  const currentUser = UserAuthService.getCurrentUser();

  useEffect(() => {
    if (currentUser) {
      checkExistingRecord();
    }
  }, [date, currentUser]);

  const checkExistingRecord = () => {
    if (!currentUser) return;

    const existing = WorkTimeService.getWorkTimeByDate(currentUser.id, date);
    if (existing) {
      setExistingRecord(existing);
      setStartTime(existing.startTime);
      setEndTime(existing.endTime);
      setBreakTime(existing.breakTime);
      setNotes(existing.notes || '');
    } else {
      setExistingRecord(null);
      setStartTime('09:00');
      setEndTime('18:00');
      setBreakTime(60);
      setNotes('');
    }
  };

  const calculateHours = (start: string, end: string, breakMinutes: number): number => {
    const [startHour, startMin] = start.split(':').map(Number);
    const [endHour, endMin] = end.split(':').map(Number);

    const startMinutes = startHour * 60 + startMin;
    let endMinutes = endHour * 60 + endMin;

    if (endMinutes < startMinutes) {
      endMinutes += 24 * 60;
    }

    const totalMinutes = endMinutes - startMinutes - breakMinutes;
    return Math.max(0, totalMinutes / 60);
  };

  const showToast = (message: string, type: 'success' | 'error') => {
    setToast({ show: true, message, type });
    setTimeout(() => {
      setToast({ show: false, message: '', type: 'success' });
    }, 3000);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!currentUser) {
      showToast('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.', 'error');
      return;
    }

    setIsLoading(true);

    try {
      const workTime = WorkTimeService.addWorkTime(
        currentUser.id,
        date,
        startTime,
        endTime,
        breakTime,
        notes.trim() || undefined
      );

      const actionText = existingRecord ? 'ÏàòÏ†ï' : 'Îì±Î°ù';
      showToast(`Í∑ºÎ¨¥ÏãúÍ∞ÑÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ${actionText}ÎêòÏóàÏäµÎãàÎã§.`, 'success');
      setExistingRecord(workTime);

      // ÏÉàÎ°úÏö¥ ÎÇ†ÏßúÎ°ú Ïù¥ÎèôÌïòÏó¨ Îã§Ïùå ÏûÖÎ†• Ï§ÄÎπÑ
      if (!existingRecord) {
        const nextDay = new Date(date);
        nextDay.setDate(nextDay.getDate() + 1);
        const nextDateString = nextDay.toISOString().split('T')[0];

        // ÎØ∏Îûò ÎÇ†ÏßúÍ∞Ä ÏïÑÎãå Í≤ΩÏö∞ÏóêÎßå Ïù¥Îèô
        const today = new Date().toISOString().split('T')[0];
        if (nextDateString <= today) {
          setDate(nextDateString);
        }
      }
    } catch (error) {
      showToast('Í∑ºÎ¨¥ÏãúÍ∞Ñ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.', 'error');
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  const quickTimeSet = (startH: number, endH: number, breakMin: number) => {
    setStartTime(`${startH.toString().padStart(2, '0')}:00`);
    setEndTime(`${endH.toString().padStart(2, '0')}:00`);
    setBreakTime(breakMin);
  };

  const currentHours = calculateHours(startTime, endTime, breakTime);
  const isValidTime = currentHours > 0 && currentHours <= 24;

  return (
    <div className="work-time-input">
      <div className="input-header">
        <div className="input-title">
          <span className="input-icon">‚è∞</span>
          <div>
            <h2>Í∑ºÎ¨¥ÏãúÍ∞Ñ ÏûÖÎ†•</h2>
            <p>Ï†ïÌôïÌïú Í∑ºÎ¨¥ÏãúÍ∞ÑÏùÑ Í∏∞Î°ùÌï¥Ï£ºÏÑ∏Ïöî</p>
          </div>
        </div>
        {existingRecord && (
          <div className="edit-indicator">
            <span className="edit-icon">‚úèÔ∏è</span>
            <span>ÏàòÏ†ï Î™®Îìú</span>
          </div>
        )}
      </div>

      <form onSubmit={handleSubmit} className="work-form">
        <div className="form-section">
          <h3 className="section-title">üìÖ Í∏∞Î≥∏ Ï†ïÎ≥¥</h3>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="date" className="form-label">ÎÇ†Ïßú</label>
              <input
                id="date"
                type="date"
                value={date}
                onChange={(e) => setDate(e.target.value)}
                max={new Date().toISOString().split('T')[0]}
                className="form-input"
                required
              />
            </div>
          </div>
        </div>

        <div className="form-section">
          <h3 className="section-title">üïê Í∑ºÎ¨¥ÏãúÍ∞Ñ</h3>

          <div className="quick-times">
            <div className="quick-times-header">Îπ†Î•∏ ÏÑ§Ï†ï</div>
            <div className="quick-time-buttons">
              <button
                type="button"
                className="quick-time-btn"
                onClick={() => quickTimeSet(9, 18, 60)}
              >
                09:00-18:00 (1ÏãúÍ∞Ñ Ìú¥Í≤å)
              </button>
              <button
                type="button"
                className="quick-time-btn"
                onClick={() => quickTimeSet(10, 19, 60)}
              >
                10:00-19:00 (1ÏãúÍ∞Ñ Ìú¥Í≤å)
              </button>
              <button
                type="button"
                className="quick-time-btn"
                onClick={() => quickTimeSet(14, 22, 30)}
              >
                14:00-22:00 (30Î∂Ñ Ìú¥Í≤å)
              </button>
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="startTime" className="form-label">ÏãúÏûë ÏãúÍ∞Ñ</label>
              <input
                id="startTime"
                type="time"
                value={startTime}
                onChange={(e) => setStartTime(e.target.value)}
                className="form-input"
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="endTime" className="form-label">Ï¢ÖÎ£å ÏãúÍ∞Ñ</label>
              <input
                id="endTime"
                type="time"
                value={endTime}
                onChange={(e) => setEndTime(e.target.value)}
                className="form-input"
                required
              />
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="breakTime" className="form-label">Ìú¥Í≤åÏãúÍ∞Ñ (Î∂Ñ)</label>
              <select
                id="breakTime"
                value={breakTime}
                onChange={(e) => setBreakTime(Number(e.target.value))}
                className="form-input"
                required
              >
                <option value={0}>0Î∂Ñ</option>
                <option value={30}>30Î∂Ñ</option>
                <option value={60}>1ÏãúÍ∞Ñ</option>
                <option value={90}>1ÏãúÍ∞Ñ 30Î∂Ñ</option>
                <option value={120}>2ÏãúÍ∞Ñ</option>
              </select>
            </div>
            <div className="form-group">
              <label className="form-label">Ï¥ù Í∑ºÎ¨¥ÏãúÍ∞Ñ</label>
              <div className={`work-hours-display ${!isValidTime ? 'invalid' : ''}`}>
                {isValidTime ? `${currentHours.toFixed(1)}ÏãúÍ∞Ñ` : 'ÏûòÎ™ªÎêú ÏãúÍ∞Ñ'}
              </div>
            </div>
          </div>
        </div>

        <div className="form-section">
          <h3 className="section-title">üìù Î©îÎ™®</h3>
          <div className="form-group">
            <textarea
              id="notes"
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="ÌäπÏù¥ÏÇ¨Ìï≠, ÏóÖÎ¨¥ ÎÇ¥Ïö© Îì±ÏùÑ Í∏∞Î°ùÌï¥Ï£ºÏÑ∏Ïöî (ÏÑ†ÌÉùÏÇ¨Ìï≠)"
              className="form-textarea"
              rows={3}
            />
          </div>
        </div>

        <div className="form-actions">
          <button
            type="submit"
            className={`submit-btn ${!isValidTime ? 'disabled' : ''}`}
            disabled={isLoading || !isValidTime}
          >
            {isLoading ? (
              <>
                <span className="loading-icon">‚ü≥</span>
                Ï†ÄÏû• Ï§ë...
              </>
            ) : (
              <>
                <span className="submit-icon">üíæ</span>
                {existingRecord ? 'ÏàòÏ†ïÌïòÍ∏∞' : 'Îì±Î°ùÌïòÍ∏∞'}
              </>
            )}
          </button>
        </div>
      </form>

      {/* Toast Notification */}
      {toast.show && (
        <div className={`work-toast ${toast.type}`}>
          <span className="toast-icon">
            {toast.type === 'success' ? '‚úÖ' : '‚ùå'}
          </span>
          <span className="toast-message">{toast.message}</span>
        </div>
      )}
    </div>
  );
};

export default WorkTimeInput;