import React, { useState } from 'react';
import { UserService } from '../services/userService';
import { PasswordUtils } from '../utils/passwordUtils';

interface PasswordChangeModalProps {
  isOpen: boolean;
  userId: string;
  onClose: () => void;
  onSuccess: () => void;
}

const PasswordChangeModal: React.FC<PasswordChangeModalProps> = ({
  isOpen,
  userId,
  onClose,
  onSuccess
}) => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
    if (!currentPassword || !newPassword || !confirmPassword) {
      setError('Î™®Îì† ÌïÑÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
      return;
    }

    if (currentPassword === newPassword) {
      setError('ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ Îã¨ÎùºÏïº Ìï©ÎãàÎã§.');
      return;
    }

    const validation = PasswordUtils.validatePassword(newPassword);
    if (!validation.valid) {
      setError(validation.errors.join(' '));
      return;
    }

    setIsLoading(true);

    try {
      // ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏
      const user = await UserService.getUserById(userId);
      if (!user || !user.password_hash) {
        setError('ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.');
        return;
      }

      const isCurrentValid = await PasswordUtils.verifyPassword(currentPassword, user.password_hash);
      if (!isCurrentValid) {
        setError('ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.');
        return;
      }

      // ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
      await UserService.changePassword(userId, newPassword);

      alert('ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.');
      onSuccess();
      onClose();
    } catch (err) {
      setError('ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      console.error('Password change error:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setCurrentPassword('');
    setNewPassword('');
    setConfirmPassword('');
    setError('');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content password-change-modal">
        <div className="modal-header">
          <h2>üîê ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω</h2>
          <button onClick={handleClose} className="modal-close">√ó</button>
        </div>

        <form onSubmit={handleSubmit} className="password-change-form">
          {error && (
            <div className="error-message">
              <span className="error-icon">‚ö†Ô∏è</span>
              {error}
            </div>
          )}

          <div className="form-group">
            <label htmlFor="currentPassword">ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ *</label>
            <input
              type="password"
              id="currentPassword"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              placeholder="ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              disabled={isLoading}
            />
          </div>

          <div className="form-group">
            <label htmlFor="newPassword">ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ *</label>
            <input
              type="password"
              id="newPassword"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder="ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              disabled={isLoading}
            />
            <p className="password-help">
              ÏµúÏÜå 4ÏûêÎ¶¨ Ïù¥ÏÉÅ, ÏµúÎåÄ 20ÏûêÎ¶¨ Ïù¥ÌïòÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.
            </p>
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ *</label>
            <input
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Îã§Ïãú ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
              disabled={isLoading}
            />
          </div>

          <div className="form-actions">
            <button
              type="button"
              onClick={handleClose}
              className="secondary-button"
              disabled={isLoading}
            >
              Ï∑®ÏÜå
            </button>
            <button
              type="submit"
              className="primary-button"
              disabled={isLoading}
            >
              {isLoading ? 'Î≥ÄÍ≤Ω Ï§ë...' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default PasswordChangeModal;